<template lang="html">
  <div class="main">
    <div class="from">
      <div class="space">
        <div class="goto">
          <div class="side show">
            <div class="left"></div>
            <div class="right"></div>
          </div>
          <div class="side write">
            <div class="left">{{$submitInfo.fromStationName}}</div>
            <img class="middle" src="../../assets/exchange-blue.png">

            <div class="right">{{$submitInfo.toStationName}}</div>
          </div>
        </div>
        <div class="group">
          <div class="prompt"></div>
          <div class="write no-prompt right">{{$submitInfo.trainCodes}}</div>
        </div>
        <div class="group no-border">
          <div class="prompt"></div>
          <div class="write no-prompt right">{{$submitInfo.seatTypes}}</div>
        </div>
      </div>
      <div class="space" v-if="isLogin">
        <div class="group no-border" @click="login">
          <div class="logo"></div>
          <div class="write">12306</div>
        </div>
      </div>
      <div class="space no-border">
        <div class="group no-border" v-for="(passenger,index) in passengers.data">
          <div class="list" @click="minus($event,index)"></div>
          <div class="write no-prompt">
            <span class="name">{{passenger.name}}</span>
            <span v-if="passenger.personType==1">{{passenger.identy}}</span>
            <span v-if="passenger.personType==2">{{passengers.data[0].name}}</span>
            <span class="info">{{passenger.piaoType}}</span>
          </div>
        </div>
        <div class="group no-border" v-for="(child,index) in $$childs">
          <div class="list" @click="minusChild($event,index)"></div>
          <div class="write no-prompt">
            <span class="name">{{child.name}}</span>
            <span>{{passengers.data[0].name}}</span>
            <span class="info"></span>
          </div>
        </div>
      </div>
      <div class="state" v-show="$$childs.length>0||passengers.child">
        ,
      </div>
      <div class="space no-padding">
        <div class="handle">
          <div class="btn" @click="contact">/</div>
          <div class="line"></div>
          <div class="btn" @click="addChild"></div>
        </div>
      </div>
      <div class="space">
        <div class="group no-border" @click="packShow">
          <div class="prompt"></div>
          <div class="write" v-if="submitInfo.buyTicketPackageId==1"></div>
          <div class="write" v-if="submitInfo.buyTicketPackageId==2">20/ </div>
          <div class="write" v-if="submitInfo.buyTicketPackageId==3">30/ </div>
        </div>
      </div>
      <div class="space">
        <div class="group no-border">
          <div class="prompt"></div>
          <input type="text" class="ipt" placeholder="" v-model="submitInfo.phone">
        </div>
      </div>
      <div class="submit" @click="submit"></div>
    </div>
    <transition name="fade">
      <div class="pack" v-show="pack" @click="packBGHide($event)">
        <div class="select">
          <div class="space_t">
            <div class="xx" @click="pack=false"></div>
            <div class="word"></div>
          </div>
          <ul>
            <li @click="packHide(2)" v-bind:class="{active:submitInfo.buyTicketPackageId==2}"><span> 20/</span>
              80<span class="know" @click.stop="insurance=true"></span>
            </li>
            <li @click="packHide(3)" v-bind:class="{active:submitInfo.buyTicketPackageId==3}"><span> 30/</span>
              160<span class="know" @click.stop="insurance=true"></span>
            </li>
            <li @click="packHide(1)" v-bind:class="{active:submitInfo.buyTicketPackageId==1}"> </li>
          </ul>
          <div class="insurance" v-show="insurance">
            <div class="word">
              20<br>

              1). 8015000<br>

              2). 1015000<br>

              30<br>

              1600-92010-17500.4<br>

              2.  3080<br>

              3. <br>

              4.  <br>

              5. <br>

              <br>

              <br>

              6. 95515<br>

              7. 2014<br>


              <br>

              1 <br>

              2 18<br>

              3 18<br>

              4 <br>

              5 <br>

              6 1<br>
            </div>

            <div class="x" @click="insurance=false"></div>
          </div>
        </div>
      </div>
    </transition>
    <contacts></contacts>
    <!--  -->
    <div class="content" v-if="show">
      <ul>
        <li>
          <label for="name"></label>
          <input type="text" name="name" id='name' value="" placeholder="">
        </li>
        <li>
          <label for="sex"></label>
          <label style="margin-left:10px;color:#999"><input type="radio" name="sex" value="0"
                                                            checked="checked"></label>
          <label style="margin-left:20px;color:#999"><input type="radio" name="sex" value="1"></label>
        </li>
        <li class="typeLi">
          <label for="birthday"></label>
          <input type="text" name="birthday" id='birthday' placeholder="20160101">
        </li>
      </ul>
      <div class="prote">1-37</div>
      <div class="sure" @click="sev"></div>
    </div>
    <message></message>
    <pay></pay>
  </div>
</template>

<script lang="babel">
  import Vue from 'vue'
  import Contacts from './Contacts.vue'
  import Message from '../Message.vue'
  import Pay from '../pay/Main.vue'

  export default {
    name: 'menu',
    components: {
      Contacts,
      Message,
      Pay
    },
    data: function () {
      return {
        submitInfo: {
          appId: "",
          uid: "",
          fromStationName: this.$store.state.station.scope.stationTHREE.station,
          fromStationCode: this.$store.state.station.scope.stationTHREE.code,
          toStationName: this.$store.state.station.scope.stationFOUR.station,
          toStationCode: this.$store.state.station.scope.stationFOUR.code,
          grabStartTime: this.$store.state.date.scope.dateTWO.code,
          grabTimeType: 1,
          firstStartTime: "",
          trainCodes: "",
          seatTypes: "0,1,2,3,4,6,O,M,P,9",
          buyTicketPackageId: 3,
          grabTicketPackageId: 4,
          phone: "",
          grabPassengers: []
        },
        pack: false,
        payInfo: {
          orderId: '',
          price: 00.0,
          bank: false,
          list: true,
          sendCtrl: true,
          timer: 60,
          cardList: '',
          peopleInfo: '',
          checkout: {},
          index: 0,
          checkCode: '',
          sn: ''
        },
        show: false,
        childs: [],
        isLogin: true,
        insurance: false
      }
    },
    created: function () {
      this.$data.submitInfo = JSON.parse(sessionStorage.getItem('robOrder'));
      this.$http.post('/userInfo/findPhone', {
        appid: this.$data.submitInfo.appId,
        uid: this.$data.submitInfo.uid
      }).then(function (res) {
        this.$data.submitInfo.phone = res.data;
      }, function () {
        console.log('');
      });
      this.$http.post('/userInfo/isLogin', {
        appid: this.$data.submitInfo.appId,
        uid: this.$data.submitInfo.uid
      }).then(function (res) {
        if (res.data == 1) {
          this.$data.isLogin = false;
        } else {
          this.$data.isLogin = true;
        }
      }, function () {
        console.log('12306');
      });
    },
    methods: {
      packBGHide: function (event) {
        if (event.target.className == "pack") {
          this.$data.pack = false;
        }
      },
      minusChild: function (event, index) {
        this.$data.childs.splice(index, 1);
      },
      minus: function (event, index) {
        this.$store.state.contact.info.splice(index, 1);
      },
      addChild: function () {
        if (this.$data.submitInfo.grabPassengers.length == 0) {
          this.$store.commit('MESSAGE_PROMPT_SHOW', {
            text: ''
          })
        } else {
          this.$data.show = !this.$data.show
        }
      },
      sev: function () {
        var addPerson = {
          uid: this.$data.submitInfo.uid,
          appid: this.$data.submitInfo.appId,
          name: document.querySelector('#name').value,
          sex: document.querySelector(':checked').value,
          birthday: document.querySelector('#birthday').value,
          personType: 2
        };
        var reg = /^[1-9][0-9]{3}(0[1-9]|1[0-2])([0-2][1-9]|3[0-1])$/;
        if (document.querySelector('#name').value == "") {
          this.$store.commit('MESSAGE_PROMPT_SHOW', {
            text: ''
          })
        } else if (document.querySelector('#birthday').value == "") {
          this.$store.commit('MESSAGE_PROMPT_SHOW', {
            text: ''
          })
        } else if (!reg.test(document.querySelector('#birthday').value)) {
          this.$store.commit('MESSAGE_PROMPT_SHOW', {
            text: ''
          })
        } else {
          this.$http.post('/contactInfo/add', JSON.stringify(addPerson)).then(function (res) {
            this.$data.show = !this.$data.show;
            let newPerson = {};
            newPerson.id = res.data;
            newPerson.name = document.querySelector('#name').value;
            newPerson.piaoType = 2;
            this.$data.childs.push(newPerson);
          }, function () {
            this.$store.commit('MESSAGE_PROMPT_SHOW', {
              text: ''
            })
          });
        }
      },
      login: function () {
        this.$router.push({
          path: '/ticket/login',
          query: {appid: this.$data.submitInfo.appId, uid: this.$data.submitInfo.uid}
        });
      },
      packShow: function () {
        this.$data.pack = true;
      },
      packHide: function (num) {
        this.$data.pack = false;
        this.$data.submitInfo.buyTicketPackageId = num;
      },
      contact: function () {
        var ary = [];
        for (var i = 0; i < this.$data.submitInfo.grabPassengers.length; i++) {
          ary.push(this.$data.submitInfo.grabPassengers[i].id)
        }
        this.$store.commit("CONTACT_OPEN", {
          ctrl: true,
          keepID: ary
        });
      },
      submit: function () {
        this.$data.submitInfo.grabPassengers = this.$data.submitInfo.grabPassengers.concat(this.$data.childs);
        let data = this.$data.submitInfo;
        if (data.seatTypes == '0,1,2,3,4,6,O,M,P,9') {
          data.seatTypes = '';
        } else {
          data.seatTypes = data.seatTypes.replace('', '0');
          data.seatTypes = data.seatTypes.replace('', '1');
          data.seatTypes = data.seatTypes.replace('', '2');
          data.seatTypes = data.seatTypes.replace('', '3');
          data.seatTypes = data.seatTypes.replace('', '4');
          data.seatTypes = data.seatTypes.replace('', '6');
          data.seatTypes = data.seatTypes.replace('', 'O');
          data.seatTypes = data.seatTypes.replace('', 'M');
          data.seatTypes = data.seatTypes.replace('', 'P');
          data.seatTypes = data.seatTypes.replace('', '9');
        }
        var reg = /^1(3|4|5|7|8)\d{9}$/;
        if (this.$data.submitInfo.grabPassengers == "") {
          this.$store.commit('MESSAGE_PROMPT_SHOW', {
            text: ''
          })
        } else if (!reg.test(this.$data.submitInfo.phone)) {
          this.$store.commit('MESSAGE_PROMPT_SHOW', {
            text: ''
          })
        } else {
          this.$http.post('/ticket/grab', data).then(function (res) {
            this.$store.commit('PAY_CALL', {
              appid: this.$data.submitInfo.appId,
              uid: this.$data.submitInfo.uid,
              orderId: res.data.grabTicketFormId,
              price: res.data.price,
              type: 'rob'
            });
          }, function () {
            this.$store.commit('MESSAGE_PROMPT_SHOW', {
              text: ''
            })
          });
        }
      }
    },
    computed: {
      $payInfo: function () {
        return this.$data.payInfo;
      },
      $$childs: function () {
        this.$data.childs.piaoType = '';
        return this.$data.childs;
      },
      passengers: function () {
        let storeDate = this.$store.state.contact.info;
        let data = [];
        let hasChild = false;
        this.$data.submitInfo.grabPassengers = [];
        for (let i = 0; i < storeDate.length; i++) {
          if (storeDate[i]) {
            if (storeDate[i].personType == 2) {
              hasChild = true;
            }
            data.push(storeDate[i]);
            this.$data.submitInfo.grabPassengers.push({
              id: storeDate[i].id,
              name: storeDate[i].name,
              piaoType: storeDate[i].personType
            });
          }
        }
        return {
          data: data,
          child: hasChild
        };
      },
      $submitInfo: function () {
        let data = this.$data.submitInfo;
        if (data.seatTypes == '0,1,2,3,4,6,O,M,P,9') {
          data.seatTypes = '';
        } else {
          data.seatTypes = data.seatTypes.replace('0', '');
          data.seatTypes = data.seatTypes.replace('1', '');
          data.seatTypes = data.seatTypes.replace('2', '');
          data.seatTypes = data.seatTypes.replace('3', '');
          data.seatTypes = data.seatTypes.replace('4', '');
          data.seatTypes = data.seatTypes.replace('6', '');
          data.seatTypes = data.seatTypes.replace('O', '');
          data.seatTypes = data.seatTypes.replace('M', '');
          data.seatTypes = data.seatTypes.replace('P', '');
          data.seatTypes = data.seatTypes.replace('9', '');
        }
        return data;
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .flexItem(@val,@width:0) {
    box-flex: @val;
    -webkit-box-flex: @val;
    -webkit-flex: @val;
    -ms-flex: @val;
    flex: @val;
    width: @width;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .3s
  }

  .fade-enter, .fade-leave-active {
    opacity: 0
  }

  .space_t {
    width: 100%;
    height: 49px;
    line-height: 49px;
    border-bottom: 1px solid #f5f5f5;
    position: relative;
    .xx {
      width: 14px;
      height: 14px;
      background: url("../../assets/xx.png") no-repeat center;
      background-size: 14px 14px;
      padding: 15px;
      position: absolute;
      top: 9px;
      left: 10px;
    }
    .word {
      font-size: 15px;
      color: #111;
      width: 100%;
      height: 49px;
      line-height: 49px;
      text-align: center;
    }
  }

  .state {
    width: 100%;
    height: auto;
    background-color: #fef0f0;
    border: 1px solid #fed4d4;
    line-height: 24px;
    font-size: 15px;
    color: #ff6565;
    text-align: center;
    padding: 15px;
  }

  .prote {
    font-size: 14px;
    color: #c1c1c1;
    float: left;
    padding: 15px;
    text-align: left;
  }

  #birthday {
    width: 200px;
  }

  .main {
    width: 100%;
    height: 100%;
    overflow: auto;
    padding-bottom: 50px;
    .flexItem(1, 100%);
    background-color: #f5f5f5;
  }

  .from {
    .space {
      padding: 0 15px;
      background-color: #FFF;
      border-bottom: 5px solid #f5f5f5;
      &.no-border {
        border: none;
      }
      &.no-padding {
        padding: 0;
      }
    }
    .handle {
      height: 56px;
      border-top: 1px solid #efefef;
      border-bottom: 1px solid #efefef;
      position: relative;
      .btn {
        width: 50%;
        height: 56px;
        line-height: 56px;
        font-size: 15px;
        color: #1ca0e2;
        float: left;
        text-align: center;
      }
      .line {
        position: absolute;
        width: 1px;
        height: 56px;
        background-color: #efefef;
        top: 0;
        left: 50%;
      }
    }
    .group {
      height: 56px;
      border-bottom: 1px dashed #efefef;
      &.no-border {
        border: none;
      }
      .prompt {
        float: left;
        font-size: 16px;
        color: #111;
        line-height: 56px;
        margin-right: 18px;
      }
      .logo {
        width: 26px;
        height: 26px;
        float: left;
        margin-top: 14px;
        margin-right: 10px;
        background: url("../../assets/12306.png") no-repeat;
        background-size: 26px 26px;
      }
      .list {
        width: 19px;
        height: 19px;
        float: left;
        margin-top: 18px;
        margin-right: 10px;
        background: url("../../assets/list.png") no-repeat;
        background-size: 19px 19px;
      }
      .write {
        &.empty {
          color: #CCC;
        }
        &.no-prompt {
          background: none;
        }
        &.right {
          text-align: right;
        }
        .info {
          float: right;
          font-size: 12px;
          color: #b6b6b6;
        }
        .name {
          font-weight: bold;
        }
        line-height: 56px;
        font-size: 16px;
        color: #111;
        text-align: left;
        background: url("../../assets/prompt-arrow.png") no-repeat right;
        background-size: 7px 12px;
      }
      .ipt {
        float: left;
        height: 54px;
        border: none;
      }
    }

  }

  .goto {
    padding: 14px 0 6px;
    border-bottom: 1px solid #efefef;
    .side {
      overflow: hidden;
      &.show {
        font-size: 12px;
        color: #999;
      }
      &.write {
        margin-top: 9px;
        font-size: 24px;
        font-weight: bold;
        color: #111;
        text-align: center;
        .middle {
          width: 27px;
          height: 27px;
          margin-top: 4px;
        }
      }
      .left {
        float: left;
      }
      .right {
        float: right;
      }
    }
  }

  .pack {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 88;
    background: rgba(0, 0, 0, 0.5);
    .select {
      position: absolute;
      width: 100%;
      height: 50%;
      background-color: #FFF;
      left: 0;
      bottom: 0;
      .xx {
        width: 14px;
        height: 14px;
        background: url("../../assets/xx.png") no-repeat center;
        background-size: 14px 14px;
        padding: 15px;
      }
      .insurance {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9999;
        width: 100%;
        height: 100%;
        padding: 25px 15px;
        background: rgba(255, 255, 255, 1);
        .word {
          height: 90%;
          text-align: left;
          overflow: auto;
        }
        .x {
          height: 10%;
          width: 100%;
          font-size: 30px;
          color: #999;
          text-align: center;
        }
      }
      ul {
        li {
          width: 100%;
          height: 45px;
          line-height: 45px;
          text-align: left;
          padding-left: 15px;
          color: #999;
          border-bottom: 1px solid #f5f5f5;
          span {
            color: #000;
          }
          .know {
            display: inline-block;
            width: 13px;
            height: 13px;
            margin-top: 5px;
            margin-left: 5px;
            background: url('../../assets/know.png') no-repeat;
            background-size: 13px 13px;
          }
          &.active {
            color: #2ba7e5;
            background: url("../../assets/select.png") no-repeat 320px;
            background-size: 16px 11px;
            span {
              font-weight: bold;
            }
          }
        }
      }
    }
  }

  .submit {
    display: block;
    width: 100%;
    height: 49px;
    line-height: 49px;
    background-color: #4ab9f1;
    font-size: 18px;
    font-weight: bold;
    color: #FFF;
    position: fixed;
    bottom: 0;
    left: 0;
  }

  .content {
    width: 100%;
    height: 100%;
    background: #fff;
    position: absolute;;
    top: 0px;
    left: 0;
    ul {
      li {
        width: 100%;
        height: 48px;
        line-height: 48px;
        font-size: 15px;
        text-align: left;
        padding: 0 15px;
        background: #fff;
        border-bottom: 1px solid #ebebeb;

        &.typeLi {
          background: url("../../assets/prompt-arrow.png") no-repeat 96%;
          background-size: 8px 11px;
        }

        input {
          border: none;
          color: #999;
          &:focus {
            outline: none;
          }
        }
      }
    }
    .sure {
      position: fixed;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 50px;
      line-height: 50px;
      font-size: 15px;
      background: #4ab9f1;
      color: #fff;
    }
  }

</style>



